.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(82, 79, 79, 0.8);
    z-index: 1200;
}
  
.modal {
    max-width: calc(100vh - 30px);
    max-height: calc(100vh - 24px);
}