.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.container_form{
    background-color: rgb(127, 202, 186);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    width: 214px;
    padding: 30px;
    border-radius: 10px;
    @media screen and (min-width: 768px) {
        width: 260px;
    }
}

.title{
    font-size: 20px;
    color: rgb(118, 121, 122);
    @media screen and (min-width: 768px) {
        font-size: 32px;
    }
}


.label{
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
    color:rgb(76, 78, 78);
    font-size: 14px;
    @media screen and (min-width: 768px) {
        font-size: 16px;
    }
}

.input{
    font-size: 12px;
    border-color: white;
    @media screen and (min-width: 768px) {
        font-size: 14px;
    }
}

.button{
    display: block;
    margin: 0 auto;
    margin-top: 30px;
    padding: 6px 10px;
    border: none;
    border-radius: 50px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    cursor: pointer;
    color: rgb(118, 121, 122);
}

.button:hover,
.button:focus{
    background-color: rgb(118, 121, 122);
    color: white;
    cursor: pointer;
}

.errors{
    margin: 5px 0;
    color: whitesmoke;
}
